<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-form
      v-if="!loading"
      ref="form2"
      v-model="valid"
      lazy-validation
      @submit.prevent="validateBeforeSubmit">
      <v-layout
        row
        wrap>
        <v-flex md12>
          <v-alert
            :value="!value.permitirEnvioSPC"
            type="warning"
          >
            <span class="subheading">ATENÇÃO: Esse cliente NÃO vai ser enviado para SPC/SERASA.</span>
          </v-alert>
          <v-alert
            v-if="value.ultimoHistoricoSPC"
            :value="true"
            type="warning"
          >
            <span class="subheading">
              CLIENTE NO {{ value.ultimoHistoricoSPC.servicoSPC }} DESDE {{ this.$moment(value.ultimoHistoricoSPC.dataCadastro).format('DD/MM/YYYY') }}.</span>
          </v-alert>
        </v-flex>
        <v-flex md12>
          <v-expansion-panel
            v-model="panel"
            expand
          >
            <v-expansion-panel-content
              :key="0"
              class="primary"
            >
              <template slot="actions">
                <v-icon color="white">mdi-chevron-down</v-icon>
              </template>
              <template slot="header">
                <span class="title white--text">Identificação</span>
              </template>
              <v-card class="elevation-0">
                <v-card-text>
                  <v-layout
                    row
                    wrap>
                    <v-flex md3>
                      <v-text-field
                        v-validate="'required'"
                        v-model="value.nome"
                        :error-messages="errors.collect('Nome')"
                        data-vv-name="Nome"
                        label="Nome"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-mask="['###.###.###-##']"
                        v-validate="'required'"
                        v-model.trim="value.cpf"
                        :error-messages="errors.collect('CPF')"
                        disabled
                        type="text"
                        data-vv-name="CPF"
                        label="CPF"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-model="value.identidade"
                        :error-messages="errors.collect('Núm. Identidade')"
                        data-vv-name="Núm. Identidade"
                        maxlength="15"
                        label="Núm. Identidade"
                      />
                    </v-flex>
                    <v-flex md1>
                      <v-text-field
                        v-validate="'required'"
                        v-model="value.orgaoEmissor"
                        :error-messages="errors.collect('Órgão Emissor')"
                        type="text"
                        data-vv-name="Órgão Emissor"
                        label="Órgão Emissor"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-model="value.dataEmissao"
                        :error-messages="errors.collect('Data Emissão')"
                        :rules="[rulesDate.dataFutura]"
                        type="date"
                        data-vv-name="Data Emissão"
                        label="Data Emissão"
                      />
                    </v-flex>
                    <v-flex md1>
                      <v-autocomplete
                        v-validate="'required'"
                        v-model="value.uf"
                        :error-messages="errors.collect('UF')"
                        :items="estados"
                        data-vv-name="UF"
                        label="UF"
                        item-text="uf"
                        item-value="uf"
                      />
                    </v-flex>
                    <v-flex md1>
                      <v-text-field
                        v-model="value.grupo"
                        label="Grupo"
                        type="text"
                        readonly
                        hint="Preenchida automaticamente pelo motor de crédito"
                      />
                    </v-flex>

                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-model="value.dataNascimento"
                        :error-messages="errors.collect('Nascimento')"
                        :rules="[rulesDate.dataFutura]"
                        type="date"
                        label="Nascimento"
                        data-vv-name="Nascimento"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-validate="'required'"
                        v-model="value.naturalEstado"
                        :error-messages="errors.collect('Estado Nascimento')"
                        :items="estados"
                        data-vv-name="Estado Nascimento"
                        label="Estado Nascimento"
                        item-text="uf"
                        item-value="uf"
                        @change="changeEstadoNascimento"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-validate="'required'"
                        v-model="value.naturalCidade"
                        :error-messages="errors.collect('Cidade Nascimento')"
                        :items="cidades"
                        :loading="loadingCidades"
                        label="Cidade Nascimento"
                        data-vv-name="Cidade Nascimento"
                        item-text="nome"
                        item-value="ibge"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-validate="'required'"
                        v-model="value.sexo"
                        :error-messages="errors.collect('Sexo')"
                        :items="sexos"
                        label="Sexo"
                        data-vv-name="Sexo"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-validate="'required'"
                        v-model="value.estadoCivil"
                        :error-messages="errors.collect('Estado Civil')"
                        :items="estadosCivil"
                        label="Estado Civil"
                        data-vv-name="Estado Civil"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-model="value.dependentes"
                        label="Dependentes"
                        type="number"
                        min="0"
                        reverse
                      />
                    </v-flex>

                    <v-flex md6>
                      <v-text-field
                        v-model="value.nomePai"
                        label="Nome do Pai"
                      />
                    </v-flex>
                    <v-flex md6>
                      <v-text-field
                        v-validate="'required'"
                        v-model="value.nomeMae"
                        :error-messages="errors.collect('Nome da Mãe')"
                        data-vv-name="Nome da Mãe"
                        label="Nome da Mãe"
                      />
                    </v-flex>

                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-mask="['(##) #### - ####', '(##) ##### - ####']"
                        :error-messages="errors.collect('Tel. Residencial')"
                        v-model="value.telResidencial"
                        label="Tel. Residencial"
                        data-vv-name="Tel. Residencial"
                        hint="<span class='body-2 red--text'>Caso o cliente não tenha telefone fixo, incluir o celular de familiar!</span>"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-mask="['(##) #### - ####', '(##) ##### - ####']"
                        :error-messages="errors.collect('Celular')"
                        v-model="value.celular"
                        label="Celular"
                        data-vv-name="Celular"
                        hint="<span class='body-2 red--text'>!Incluir o celular do cliente, qualquer informação será enviada para esse número!</span>"
                      />
                    </v-flex>
                    <v-flex md3>
                      <v-text-field
                        v-validate="'required|email'"
                        v-model="value.email"
                        :error-messages="errors.collect('Email')"
                        type="email"
                        label="Email"
                        data-vv-name="Email"
                        hint="<span class='body-2 red--text'>!caso cliente não possua email, digitar nao@email.com.br</span>"
                      />
                    </v-flex>
                    <v-flex md3>
                      <v-autocomplete
                        v-model="value.escolaridade"
                        :items="escolaridades"
                        label="Escolaridade"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-formata-moeda="value.rendaPrincipal"
                        :error-messages="errors.collect('Renda Principal')"
                        v-model="value.rendaPrincipal"
                        v-money="money"
                        suffix="R$"
                        label="Renda Principal"
                        reverse
                      />
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>
            <v-expansion-panel-content
              v-if="editando && !loadingResumo"
              :key="1"
              class="primary mt-1"
            >
              <template slot="actions">
                <v-icon color="white">mdi-chevron-down</v-icon>
              </template>
              <template slot="header">
                <span class="title white--text">Carteira do Cliente</span>
              </template>
              <v-card
                class="elevation-0"
              >
                <v-card-text>
                  <v-layout
                    row
                    wrap>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Rede'"
                        :value="resumoCliente.nomeRede"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Empresa'"
                        :value="resumoCliente.nomeEmpresa"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Loja'"
                        :value="resumoCliente.nomeLoja"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Data Assinatura'"
                        :value="resumoCliente.dataAssinatura | moment('DD/MM/YYYY')"
                      />
                    </v-flex>

                    <v-flex
                      md6
                      xs12>
                      <div>
                        <span class="body-2">Situação do Contrato</span><br>
                        <span
                          class="body-1"
                          v-html="situacaoContrato(resumoCliente)"
                        />
						<v-btn
							v-if="resumoCliente.status === 'NORMAL'"
						  	class="ma-2"
						  	color="red"
							dark>
							Bloquear
						  <v-icon
						  	dark
						  	right
							@click="bloquearCliente">
						  >
						  	mdi-cancel
       				      </v-icon>
						</v-btn>
						<v-btn
							v-else-if="$root.isAdminSup()"
						  	class="ma-2"
						  	color="primary"
							dark>
							Desbloquear
						  <v-icon
						  	dark
						  	right
							@click="desbloquearCliente">
						  >
						  	mdi-checkbox-marked-circle
       				      </v-icon>
						</v-btn>
                      </div>
					</v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
					  	v-if="!flagIncluir"
                        :label="'Limite Crédito Compartilhado Total'"
                        :value="formatValorMonetario(value.limiteCompartilhado)"
                      />
					  <v-icon
					    v-if="$root.isAdminSup()"
				  	    outline
						color="primary"
						@click="incluirValor">
						mdi-pencil
					  </v-icon>
					  <template
					  	v-if="flagIncluir">
						<v-text-field
							v-money="money"
							v-model="value.limiteCompartilhado"
							:error-messages="errors.collect('Limite Crédito Compartilhado Total')"
							data-vv-name="Limite Crédito Compartilhado Total"
							prefix="R$"
							label="Limite Crédito Compartilhado Total"
						/>
						<v-btn
							:loading="loadingBtn"
							small
							color="primary"
							elevation="5"
							dark
							@click="alterarLimite">
        					<v-icon
							dark
							right>mdi-checkbox-marked</v-icon>
      						</v-btn>
						<v-btn
							small
							color="error"
							elevation="5"
							dark
							@click="cancelarValor">
							<v-icon
							dark
							right>mdi-close-box</v-icon>
							</v-btn>
						</template>
						</v-flex>
					<v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Limite Crédito Exclusivo Total'"
                        :value="formatValorMonetario(resumoCliente.limiteExclusivo)"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Saldo Devedor Total'"
                        :value="formatValorMonetario(resumoCliente.saldoDevedorTotal)"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Saldo Devedor no Mês'"
                        :value="formatValorMonetario(resumoCliente.saldoDevedorMes)"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Saldo Disponível Compartilhado Total'"
                        :value="formatValorMonetario(resumoCliente.limiteCompartilhadoDisponivel)"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Saldo Disponível Exclusivo Total'"
                        :value="formatValorMonetario(resumoCliente.limiteExclusivoDisponivel)"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Última Compra em'"
                        :value="resumoCliente.ultimaCompraEm | moment('DD/MM/YYYY')"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Total de Compras'"
                        :value="totalCompras()"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Último Pagamento em'"
                        :value="resumoCliente.ultimoPagamentoEm | moment('DD/MM/YYYY')"
                      />
                    </v-flex>
                    <v-flex
                      md3
                      sm6
                      xs12>
                      <core-label-value
                        :label="'Total de Pagamentos'"
                        :value="totalPagamentos()"
                      />
                    </v-flex>
                    <v-flex
                      md12
                    >
                      <v-card class="elevation-0">
                        <v-card-title>
                          <span class="title">Outras Contas do Cliente</span>
                        </v-card-title>
                        <v-card-text>
                          <v-data-table
                            :headers="headersOutrasLojas"
                            :items="resumoOutrasLojas"
                            hide-actions
                          >
                            <template
                              slot="items"
                              slot-scope="{ item }"
                            >
                              <tr>
                                <td>
                                  <span class="body-1">{{ item.nomeLoja }}</span>
                                </td>
                                <td>
                                  <span
                                    class="body-1"
                                    v-html="situacaoContrato(item)"
                                  />
                                </td>
                                <td class="body-1 text-xs-right">
                                  <span class="body-1">{{ formatValorMonetario(item.saldoDevedorTotal) }}</span>
                                </td>
                                <td class="body-1 text-xs-right">
                                  <span class="body-1">{{ formatValorMonetario(item.limiteCompartilhadoDisponível - item.limiteExclusivoDisponível) }}</span>
                                </td>
                                <td class="body-1 text-xs-right">
                                  <span class="body-1">{{ formatValorMonetario(item.limiteExclusivo) }}</span>
                                </td>
                                <td class="text-xs-right">
                                  <v-icon
                                    class="mr-2"
                                    @click="visualizarCliente(item)"
                                  >
                                    mdi-magnify
                                  </v-icon>
                                </td>
                              </tr>
                            </template>
                          </v-data-table>
                        </v-card-text>
                      </v-card>
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>
            <core-endereco
              v-model="value.endereco"
              :disabled="disabled"
              :expanded="!cobranca"
              :class-panel="'mt-1'"
            >
              <v-layout
                row
                wrap>
                <v-flex md3>
                  <v-autocomplete
                    v-validate="'required'"
                    :error-messages="errors.collect('Tipo de Residência')"
                    v-model="value.tipoResidencia"
                    :items="tiposResidencia"
                    data-vv-name="Tipo de Residência"
                    label="Tipo de Residência"
                    item-value="id"
                    item-text="descricao"
                  />
                </v-flex>
                <v-flex md3>
                  <v-text-field
                    v-mask="['##/####']"
                    v-model="value.resideDesde"
                    :rules="[rulesDesde.dataFutura]"
                    label="Reside desde MM/AAAA"
                  />
                </v-flex>
              </v-layout>
            </core-endereco>

            <template
              v-if="!cobranca"
            >
              <v-expansion-panel-content
                :key="2"
                class="primary mt-1"
              >
                <template slot="actions">
                  <v-icon color="white">mdi-chevron-down</v-icon>
                </template>
                <template slot="header">
                  <span class="title white--text">Dados Profissionais</span>
                </template>
                <v-card class="elevation-0">
                  <v-card-text>
                    <v-layout
                      row
                      wrap>
                      <v-flex md3>
                        <v-text-field
                          v-validate="'required'"
                          :error-messages="errors.collect('Empresa')"
                          v-model="value.empresa"
                          label="Empresa"
                          data-vv-name="Empresa"
                          hint="<span class='body-2 red--text'>Cliente Autônomo ou Aposentado? Incluir nesse campo nome completo de parente próximo e no campo 'Tel. Comercial' telefone desse parente.</span>"
                        />
                      </v-flex>
                      <v-flex md2>
                        <v-text-field
                          v-validate="'required'"
                          :error-messages="errors.collect('Cargo')"
                          v-model="value.cargo"
                          label="Cargo"
                          data-vv-name="Cargo"
                        />
                      </v-flex>
                      <v-flex md3>
                        <v-text-field
                          v-validate="'required'"
                          v-mask="['##/####']"
                          :error-messages="errors.collect('Trabalha Nessa Empresa desde MM/AAAA')"
                          v-model="value.trabalhaNessaEmpresaDesde"
                          :rules="[rulesDesde.dataFutura]"
                          label="Trabalha Nessa Empresa desde MM/AAAA"
                          data-vv-name="Trabalha Nessa Empresa desde MM/AAAA"
                        />
                      </v-flex>
                      <v-flex md3>
                        <v-text-field
                          v-validate="'required'"
                          v-mask="['(##) #### - ####', '(##) ##### - ####']"
                          :error-messages="errors.collect('Tel. Comercial')"
                          v-model="value.telComercial"
                          label="Tel. Comercial"
                          data-vv-name="Tel. Comercial"
                          hint="<span class='body-2 red--text'>Cliente Assalariado: Incluir telefone da empresa que o cliente está trabalhando;
                            <br/>
                            <br/>Cliente Autônomo: Incluir telefone de alguma pessoa que o cliente presta ou prestou serviço ou familiar</span>"
                        />
                      </v-flex>
                      <v-flex md1>
                        <v-text-field
                          v-mask="['####']"
                          v-model="value.ramal"
                          label="Ramal"/>
                      </v-flex>
                    </v-layout>
                  </v-card-text>
                </v-card>
              </v-expansion-panel-content>
            </template>
            <v-expansion-panel-content
              :key="3"
              class="primary mt-1"
            >
              <template slot="actions">
                <v-icon color="white">mdi-chevron-down</v-icon>
              </template>
              <template slot="header">
                <span class="title white--text">Referências Pessoais</span>
              </template>
              <v-card class="elevation-0">
                <v-card-text>
                  <span class="caption">
                    Referência Pessoal 1 e 2: Incluir nome completo  e telefone de pessoas próximas (preferencialmente familiares).
                  </span>
                  <v-layout
                    row
                    wrap>
                    <v-flex md8>
                      <v-text-field
                        v-validate="'required'"
                        :error-messages="errors.collect('Nome')"
                        v-model="value.nomeRef1"
                        label="Nome"
                        data-vv-name="Nome"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-validate="'required'"
                        :error-messages="errors.collect('Relação')"
                        v-model="value.relacaoRef1"
                        :items="tiposReferenciasPessoais"
                        data-vv-name="Relação"
                        label="Relação"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-mask="['(##) #### - ####', '(##) ##### - ####']"
                        :error-messages="errors.collect('Telefone Contato')"
                        v-model="value.telRef1"
                        label="Telefone Contato"
                        data-vv-name="Telefone Contato"
                      />
                    </v-flex>

                    <v-flex md8>
                      <v-text-field
                        v-validate="'required'"
                        :error-messages="errors.collect('Nome')"
                        v-model="value.nomeRef2"
                        data-vv-name="Nome"
                        label="Nome"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-validate="'required'"
                        :error-messages="errors.collect('Relação')"
                        v-model="value.relacaoRef2"
                        :items="tiposReferenciasPessoais"
                        label="Relação"
                        data-vv-name="Relação"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-validate="'required'"
                        v-mask="['(##) #### - ####', '(##) ##### - ####']"
                        :error-messages="errors.collect('Telefone Contato')"
                        v-model="value.telRef2"
                        label="Telefone Contato"
                        data-vv-name="Telefone Contato"
                      />
                    </v-flex>

                    <v-flex md8>
                      <v-text-field
                        v-model="value.nomeRef3"
                        label="Nome"/>
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-model="value.relacaoRef3"
                        :items="tiposReferenciasPessoais"
                        label="Relação"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-mask="['(##) #### - ####', '(##) ##### - ####']"
                        v-model="value.telRef3"
                        label="Telefone Contato"/>
                    </v-flex>

                    <v-flex md8>
                      <v-text-field
                        v-model="value.nomeRef4"
                        label="Nome"/>
                    </v-flex>
                    <v-flex md2>
                      <v-autocomplete
                        v-model="value.relacaoRef4"
                        :items="tiposReferenciasPessoais"
                        label="Relação"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-mask="['(##) #### - ####', '(##) ##### - ####']"
                        v-model="value.telRef4"
                        label="Telefone Contato"/>
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>

            <template v-if="$root.isAdmin() && !cobranca">
              <v-expansion-panel-content
                :key="4"
                class="primary mt-1"
              >
                <template slot="actions">
                  <v-icon color="white">mdi-chevron-down</v-icon>
                </template>
                <template slot="header">
                  <span class="title white--text">Permissão de Envio ao SPC/SERASA</span>
                </template>
                <v-card class="elevation-0">
                  <v-card-text>
                    <v-checkbox
                      v-model="value.permitirEnvioSPC"
                      label="Permitir o envio do cliente para SPC/SERASA"
                    />
                  </v-card-text>
                </v-card>
              </v-expansion-panel-content>
            </template>

            <template
              v-if="!cobranca && editando"
            >
              <v-expansion-panel-content
                :key="5"
                class="primary mt-1"
              >
                <template slot="actions">
                  <v-icon color="white">mdi-chevron-down</v-icon>
                </template>
                <template slot="header">
                  <span class="title white--text">Observações</span>
                </template>
                <v-card class="elevation-0">
                  <v-toolbar
                    flat
                    color="white">
                    <v-toolbar-title>Observações</v-toolbar-title>
                    <v-divider
                      class="mx-2"
                      inset
                      vertical />
                    <v-spacer />
                    <v-btn
                      small
                      fab
                      color="primary">
                      <v-icon @click="novaObservacao()">mdi-plus</v-icon>
                    </v-btn>
                  </v-toolbar>
                  <v-card-text>
                    <v-data-table
                      :headers="headersObs"
                      :items="observacoes"
                      hide-actions
                    >
                      <template
                        slot="items"
                        slot-scope="{ item }"
                      >
                        <tr>
                          <td class="text-xs-center">{{ item.dataCadastro | moment('DD/MM/YYYY') }}</td>
                          <td>{{ item.descricao }}</td>
                        </tr>
                      </template>
                    </v-data-table>
                  </v-card-text>
                </v-card>
              </v-expansion-panel-content>
            </template>

            <!-- CRUZAR DADOS -->
            <template
              v-if="$root.isRolesPratico()"
            >
              <v-expansion-panel-content
                :key="7"
                class="primary mt-1"
              >
                <template slot="actions">
                  <v-icon color="white">mdi-chevron-down</v-icon>
                </template>
                <template slot="header">
                  <span class="title white--text">Cruzar Dados</span>
                </template>
                <cliente-cruzar-dados
                  v-model="value"
                  :expanded="true"
                />
              </v-expansion-panel-content>
            </template>

            <template>
              <slot/>
            </template>
          </v-expansion-panel>
        </v-flex>
        <v-flex
          v-if="!cobranca"
          md12>
          <v-layout class="justify-end">
            <v-btn
              dark
              @click="goBack">{{ editando ? 'Sair' : 'Voltar' }}</v-btn>
            <v-btn
              :disabled="!valid"
              :loading="loadingBtn"
              type="submit"
              color="primary">{{ editando ? 'Salvar' : 'Continuar' }}</v-btn>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-form>
    <template>
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="flagAddObs"
          max-width="390">
          <v-card>
            <v-card-title class="headline">Nova Observação</v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <v-textarea
                    v-model="observacao"
                    label="Observação"
                    counter
                    maxlength="250"
                  />
                </v-flex>
                <v-flex md12>
                  <v-layout class="justify-end">
                    <v-btn
                      color="primary"
                      @click="salvarObservacao">Salvar</v-btn>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
    <template>
      <v-layout
        row
        justify-center>
        <v-dialog
          v-model="dialogLimiteCredito"
          max-width="390">
          <v-card>
            <v-card-title class="headline">Limite de Crédito Pré-Aprovado</v-card-title>

            <v-card-text>
              <v-spacer />
              <p
                v-if="value.limitesPreAprovado"
                class="title"
              >
                <span class="subheading">Primeira compra autorizada em</span>
                {{ 'R$ ' + value.limitesPreAprovado.faixaFinal3.toLocaleString('pt-BR', { minimumFractionDigits: 2 }) }}
                <span class="subheading">com entrada de </span>
                {{ ' R$ '+(value.limitesPreAprovado.faixaFinal3 * (value.limitesPreAprovado.peEntrada3/100)).toLocaleString('pt-BR', { minimumFractionDigits: 2 })+ " ("+value.limitesPreAprovado.peEntrada3.toLocaleString('pt-BR', { minimumFractionDigits: 2 })+"%)" }}
              </p>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                color="primary"
                @click="dialogLimiteCredito = false">Ok</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </v-container>
</template>

<script>
import {
  ClienteBusiness, EstadoBusiness, TiposBusiness
} from '../../business'
import {
  MONEY
} from '../../constants'
import numberUtils from '../../utils/numberUtils'
import dateUtils from '../../utils/dateUtils'

const ClienteCruzarDados = () => import('./ClienteCruzarDados.vue')

export default {
  components: {
    ClienteCruzarDados
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    },
    // eslint-disable-next-line vue/require-default-prop
    goBack: {
      type: Function
    },
    // eslint-disable-next-line vue/require-default-prop
    disabled: false,
    // eslint-disable-next-line vue/require-default-prop
    editando: false,
    cobranca: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      panel: [true, true, true, true, true, true, true],
      loading: false,
      loadingResumo: false,
      loadingResumoOutrasLojas: false,
      valid: true,
      sexos: [],
      estadosCivil: [],
      escolaridades: [],
      tiposResidencia: [],
      tiposReferenciasPessoais: [],
      estados: [],
      cidades: [],
      observacoes: [],
      loadingCidades: false,
      money: MONEY,
      loadingBtn: false,
      flagAddObs: false,
	  flagIncluir: false,
      observacao: null,
      dialogLimiteCredito: false,
      resumoCliente: null,
	  resumoOutrasLojas: [],
      hoje: this.$moment().format('YYYY-MM-DD'),
      formCobranca: {
        dataAgendamento: null,
        tipoOcorrencia: null,
        dtAgendamento: null,
        horaAgendamento: null
      },
	  alteraStatus: 0,
      tiposOcorrencia: [],
      ocorrenciasCobranca: [],
      totalOcorrencias: 0,
      paginationCobranca: {
        rowsPerPage: 15
      },
      rulesDate: {
        dataFutura: value => {
          return dateUtils.validaDataFutura(value)
        }
      },
      rulesDesde: {
        dataFutura: value => {
          if (!dateUtils.validaDesdeMMYYYY(value)) {
            return 'O período tem que ser menor que a data atual'
          }
          return true
        }
      },
      headersObs: [
        {
          sortable: false,
          text: 'Data',
          value: 'data'
        },
        {
          sortable: false,
          text: 'Observação',
          value: 'observacao',
          align: 'left',
          width: '100%'
        }
      ],
      headersOutrasLojas: [
        {
          sortable: false,
          text: 'Nome Loja',
          value: 'nomeLoja'
        },
        {
          sortable: false,
          text: 'Situação',
          value: 'situacao',
          align: 'left'
        },
        {
          sortable: false,
          text: 'Saldo Devedor',
          align: 'right',
          value: 'saldoDevedor',
          width: '20%'
        },
        {
          sortable: false,
          text: 'Saldo Disponível',
          align: 'right',
          value: 'saldoDisponivel'
        },
        {
          sortable: false,
          text: 'Limite Exclusivo',
          align: 'right',
          value: 'limiteExclusivo'
        }
      ]
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    },
    paginationCobranca: {
      handler () {
        this.paginarCobranca()
      }
    }
  },
  beforeMount () {
    if (!this.editando && !this.value.id && !this.cobranca) {
      this.dialogLimiteCredito = true
    }
    this.reload()
    if (this.cobranca) {
      this.panel = [true, true, false, true, true, true]
    }
  },
  methods: {
    validateBeforeSubmit () {
      var self = this
      this.$validator.validate().then(result => {
        if (result) {
          if (numberUtils.convertStringToNumber(self.value.rendaPrincipal) < 100) {
            this.$root.showAlerta('A Renda Principal deve ser de no mínimo <span class="title">R$ 100,00</span>')
            return
          }
          if (this.validaTelefones()) {
            this.loadingBtn = true
            ClienteBusiness.salvar(this.value).then((response) => {
              this.$root.showSucesso('Dados enviados com sucesso')
              this.setData(response.data)
              this.nextStep()
            }).catch(() => this.$root.showErro('Erro ao cadastrar cliente'))
              .finally(() => {
                this.loadingBtn = false
              })
          } else {
            this.$root.showAlerta('Todos os telefones informados precisam ser diferentes.')
          }
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    validaTelefones () {
      return this.value.telResidencial !== this.value.celular &&
      this.value.telResidencial !== this.value.telRef1 &&
      this.value.telResidencial !== this.value.telRef2 &&
      this.value.celular !== this.value.telRef1 &&
      this.value.celular !== this.value.telRef2 &&
      this.value.telRef1 !== this.value.telRef2
    },
    cancelar () {
      this.$router.push('/lista-cliente')
    },
	alterarLimite () {
	  this.loadingBtn = true
	  ClienteBusiness.alterarLimite(this.value.id, this.value.limiteCompartilhado)
	  .then(response => {
		this.$root.showSucesso(response.data)
		this.flagIncluir = false
		this.reload()
	  })
	  .catch(erro => {
		this.$root.showErro(erro.data)
	  })
	  .finally(() => {
		this.loadingBtn = false
	  })
	},
	bloquearCliente () {
	  this.loadingBtn = true
	  ClienteBusiness.alterarStatus(this.value.id, this.value.lojaId, 'BLOQUEADO')
	  .then(response => {
		this.$root.showSucesso(response.data)
		this.reload()
	  })
	  .catch(erro => {
		this.$root.showErro(erro.data)
	  })
	  .finally(() => {
		this.loadingBtn = false
	  })
	},
	desbloquearCliente () {
	  this.loadingBtn = true
	  ClienteBusiness.alterarStatus(this.value.id, this.value.lojaId, 'NORMAL')
	  .then(response => {
		this.$root.showSucesso(response.data)
		this.reload()
	  })
	  .catch(erro => {
		this.$root.showErro(erro.data)
	  })
	  .finally(() => {
		this.loadingBtn = false
	  })
	},
    reload () {
      this.loading = true
	  TiposBusiness.getAllSexo().then(response => { this.sexos = response.data })
      TiposBusiness.getAllEstadoCivil().then(response => { this.estadosCivil = response.data })
      TiposBusiness.getAllEscolaridade().then(response => { this.escolaridades = response.data })
      TiposBusiness.getAllTipoResidencia().then(response => { this.tiposResidencia = response.data })
      TiposBusiness.getAllTipoReferenciaPessoal().then(response => { this.tiposReferenciasPessoais = response.data })

      EstadoBusiness.findAll()
        .then(response => {
          this.estados = response.data
        })
        .catch(() =>
          this.$root.showAlerta(
            'Não foi possível buscar as informações de estado'
          )
        )
      this.carregarCidades(this.value.naturalEstado)
      if (this.editando) {
        this.loadResumoCliente()
        this.loadResumoClienteOutrasLojas()
      } else {
        this.loading = false
      }
      this.loadObservacoes()
	  this.loadingBtn = false
    },
    carregarCidades (estado) {
      this.value.naturalEstado = estado
      if (this.value.naturalEstado) {
        this.loadingCidades = true
        EstadoBusiness.getCidadesByUf(this.value.naturalEstado)
          .then(response => {
            this.cidades = response.data
          })
          .catch(() => {
            this.$root.showAlerta(
              'Não foi possível buscar as informações das cidades'
            )
          })
          .finally(() => {
            this.loadingCidades = false
          })
      }
    },
    changeEstadoNascimento (estado) {
      console.log('changeEstadoNascimento')
      this.carregarCidades(estado)
      this.value.naturalCidade = null
    },
    novaObservacao () {
      this.observacao = null
      this.flagAddObs = true
    },
	cancelarValor () {
	  this.loadingBtn = true
	  this.flagIncluir = false
	  this.loadingBtn = false
	},
	incluirValor () {
	  this.loadingBtn = true
	  this.flagIncluir = true
	  this.loadingBtn = false
    },
    salvarObservacao () {
      let _formObservacao = {
        descricao: this.observacao
      }
      this.loading = true
      ClienteBusiness.inserirObservacao(this.value.id, _formObservacao)
        .then(response => {
          this.loadObservacoes()
          this.flagAddObs = false
        }).catch(erro => {
          this.$root.showErro(erro.data)
          this.loading = false
        })
    },
    loadResumoCliente () {
      this.loadingResumo = true
      ClienteBusiness.loadResumoCliente(this.value.id, this.value.lojaId)
        .then(response => {
          this.resumoCliente = response.data
          this.loadingResumo = false
        }).catch(erro => {
          this.$root.showErro(erro.data)
          this.loadingResumo = false
        })
    },
    loadResumoClienteOutrasLojas () {
      this.loadingResumoOutrasLojas = true
      ClienteBusiness.loadResumoClienteOutrasLojas(this.value.id, this.value.lojaId)
        .then(response => {
          this.resumoOutrasLojas = response.data
        }).catch(erro => {
          this.$root.showErro(erro.data)
          this.loadingResumoOutrasLojas = false
        })
    },
    situacaoContrato (_resumoCliente) {
      let dadosSaida = ''

      if (_resumoCliente.atrasoDias) {
        dadosSaida += 'EM ATRASO desde ' +
        this.$moment(_resumoCliente.atrasoVencto).format('DD/MM/YYYY') +
        ' (' + _resumoCliente.atrasoDias + ' dias) <br> '
      }
      if (_resumoCliente.status) {
        let status = _resumoCliente.status
        if (status === 'NORMAL') {
          status = '<span class="green--text">NORMAL</span>'
        }
        if (status === 'BLOQUEADO') {
          status = '<span class="red--text">BLOQUEADO</span>'
        }

        dadosSaida += status +
        ' desde ' +
        this.$moment(_resumoCliente.dataStatus).format('DD/MM/YYYY') + '<br>'
      }
      if (_resumoCliente.maiorAtrasoAteHojeDias) {
        dadosSaida += 'MAIOR ATRASO ATÉ HOJE: ' +
        _resumoCliente.maiorAtrasoAteHojeDias +
        ' dia(s) - vencto ' +
        this.$moment(_resumoCliente.maiorAtrasoAteHojeVencto).format('DD/MM/YYYY') + '<br>'
      }
      if (_resumoCliente.maiorAtrasoPrim12mesesDias) {
        dadosSaida += 'MAIOR ATRASO PRIM. 12 MESES: ' +
        _resumoCliente.maiorAtrasoPrim12mesesDias +
        ' dia(s) - vencto ' +
        this.$moment(_resumoCliente.maiorAtrasoPrim12mesesVencto).format('DD/MM/YYYY')
      }
      // 'EM ATRASO desde 14/04/2020 (91 dias) <br>' +
      // 'BLOQUEADO em 13/06/2020 as 00:35 (Em Cobrança)'
      // 'MAIOR ATRASO ATÉ HOJE: 92 dias(s) - vencto data'
      // 'MAIOR ATRASO PRIM. 12 MESES: 92 dias(s) - vencto data'

      return dadosSaida
    },
    totalCompras () {
      if (this.resumoCliente.qtdTotalCompras) {
        return this.resumoCliente.qtdTotalCompras +
        ' compras(s) no total de ' +
        this.formatValorMonetario(this.resumoCliente.vlTotalCompras)
      }
      return '0'
    },
    totalPagamentos () {
      if (this.resumoCliente.qtdTotalPagamentos) {
        return this.resumoCliente.qtdTotalPagamentos +
        ' pagto(s) no total de ' +
        this.formatValorMonetario(this.resumoCliente.vlTotalPagamentos)
      }
      return '0'
    },
    formatValorMonetario (valor) {
      return 'R$ ' + numberUtils.formatValorMonetario(valor)
    },
    loadObservacoes () {
      this.loading = true
      ClienteBusiness.getAllObservacao(this.value.id)
        .then(response => {
          this.observacoes = response.data
        }).finally(() => {
          this.loading = false
        })
    },
    visualizarCliente (item) {
      if (this.cobranca) {
        this.$router.push(`/cliente-cobranca/${item.clienteId}/${item.lojaId}`)
      } else {
        this.$router.push(`/cad-cliente/${item.clienteId}/${item.lojaId}`)
      }
      this.loading = true
      window.location.reload()
    }
  }
}
</script>
